'use client'

import { useState } from 'react'
import { OpenAI } from 'openai'

const openai = new OpenAI({
  apiKey: process.env.NEXT_PUBLIC_OPENAI_API_KEY,
  dangerouslyAllowBrowser: true
})

export default function GeneratePage() {
  const [prompt, setPrompt] = useState('')
  const [loading, setLoading] = useState(false)
  const [imageUrl, setImageUrl] = useState('')
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')
    
    try {
      const enhancedPrompt = `A pixel art Pokemon in GameBoy style (4 colors only, black and 3 shades of green). ${prompt}. The image should look like it's from an original GameBoy Pokemon game.`
      
      const response = await openai.images.generate({
        prompt: enhancedPrompt,
        n: 1,
        size: "256x256",
        response_format: "url",
      })

      if (response.data[0]?.url) {
        setImageUrl(response.data[0].url)
      } else {
        throw new Error('No image was generated')
      }
    } catch (err) {
      console.error('Generation error:', err)
      setError('Failed to generate image. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="gameboy-container">
      <h1 className="text-2xl text-center mb-6 text-[#0F380F]">
        Generate Pokemon
      </h1>

      <div className="gameboy-screen mb-6">
        <form onSubmit={handleSubmit} className="space-y-4">
          <textarea
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            placeholder="Describe your Pokemon... (e.g., 'A fire-type dragon with wings')"
            className="w-full p-2 bg-[#9BBC0F] text-[#0F380F] rounded"
            rows={4}
          />
          
          <button 
            type="submit" 
            className="gameboy-button w-full"
            disabled={loading}
          >
            {loading ? 'Generating...' : 'Generate'}
          </button>
        </form>
      </div>

      <div className="gameboy-screen">
        {error && (
          <p className="text-red-500 mb-4">{error}</p>
        )}
        {imageUrl && (
          <div className="flex flex-col items-center">
            <img 
              src={imageUrl} 
              alt="Generated Pokemon" 
              className="max-w-full h-auto pixelated border-4 border-[#306230]"
            />
            <a 
              href={imageUrl}
              download="pokemon.png"
              className="gameboy-button mt-4"
              target="_blank"
              rel="noopener noreferrer"
            >
              Download
            </a>
          </div>
        )}
      </div>
    </div>
  )
}
